import React from 'react'

export default function DemoLogo () {
  const styles = {
    height: '14px',
    transform: 'scale(1.3)',
    'transform-origin': '0 50%'
  }

  return <svg viewBox='0 0 75 20' version='1.1' style={styles}>
    <g>
      <text stroke='#8e8e90' id='svg_1' y='16' x='0' fill='#8e8e90'>ABCDEF</text>
    </g>
  </svg>
}



